.chat-box-container {
    display: flex;
    flex-direction: row;
  }
  
  .chat-avatar {
    width: 45px;
    height: 45px;
  }
  
  .chat-message {
    padding: 10px;
    border-radius: 10px;
    max-width: 60%;
  }
  
  .chat-box-receiver {
    justify-content: flex-start;
  }
  
  .chat-box-sender {
    justify-content: flex-end;
  }
  
  .chat-message-receiver {
    background-color: #dcf8c6;
  }
  
  .chat-message-sender {
    background-color: #fff;
  }
  
  .chat-username {
    font-size: 13px;
  }
  